import React, { useState, useEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";

import "../styles/Navbar.scss";

function Navbar(props) {
  let history = useHistory();
  let location = useLocation().pathname;
  const [inputSearch, setInputSearch] = useState("");
  const [showBackBtn, setShowBackBtn] = useState(false);

  const handleInputChange = (e) => {
    setInputSearch(e.target.value);
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    history.push("/movieInfoApp/search/" + inputSearch);
    setInputSearch("");
  };

  useEffect(() => {
    console.log(location);
    if (location === "/movieInfoApp/") {
      setShowBackBtn(false);
    } else {
      setShowBackBtn(true);
    }
  }, [location]);

  const handleBack = () => {
    history.goBack();
  };

  return (
    <nav className="navbar">
      {showBackBtn && (
        <button onClick={handleBack} className="navbar__backBtn">
          â—„
        </button>
      )}
      <Link to="/movieInfoApp/" className="navbar__logo">
        <span>Movie</span> Info App
      </Link>
      <form onSubmit={handleSearchSubmit} className="navbar__form">
        <input
          type="text"
          placeholder="Search for movies, tv series & more..."
          value={inputSearch}
          onChange={handleInputChange}
          className="navbar__input"
        />
        <button type="submit" className="navbar__submit">
          Wyslij
        </button>
      </form>
    </nav>
  );
}

export default Navbar;
