import React, { useState, useEffect } from "react";
import axios from "axios";

import "../styles/SearchedMovies.scss";

import MovieCard from "./MovieCard";

function SearchedMovies({ match }) {
  const [searchedMovies, setSearchedMovies] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://www.omdbapi.com/?apikey=d1dd04b&s=" + match.params.inputSearch
      )
      .then(function (response) {
        // handle success
        setSearchedMovies(response.data.Search);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
  }, [match.params.inputSearch]);

  return (
    <div
      className={
        searchedMovies
          ? "searchedMovies searchedMovies--noEmpty"
          : "searchedMovies searchedMovies--empty"
      }
    >
      {searchedMovies ? (
        searchedMovies.map((movie) => {
          return (
            <MovieCard
              title={movie.Title}
              imgSrc={movie.Poster}
              year={movie.Year}
              id={movie.imdbID}
              key={movie.imdbID}
            />
          );
        })
      ) : (
        <h4 className="searchedMovies__empty">
          Ooops! There is no movie that matches your query.
        </h4>
      )}
    </div>
  );
}

export default SearchedMovies;
