import React, { useState, useEffect } from "react";
import axios from "axios";

import "../styles/MovieInfo.scss";

function MovieInfo({ match }) {
  const [movie, setMovie] = useState("");
  useEffect(() => {
    axios
      .get("http://www.omdbapi.com/?apikey=d1dd04b&i=" + match.params.movieId)
      .then(function (response) {
        // handle success
        setMovie(response.data);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
  }, [match.params.movieId]);

  console.log(movie);

  return (
    <div className="movieInfo">
      <img src={movie.Poster} alt={movie.Title} className="movieInfo__poster" />
      <div className="movieInfo__infos">
        <h3 className="movieInfo__title">{movie.Title}</h3>
        <h5 className="movieInfo__info">
          Director:{" "}
          <span className="movieInfo__infoSpan">{movie.Director}</span>
        </h5>
        <h5 className="movieInfo__info">
          Writer: <span className="movieInfo__infoSpan">{movie.Writer}</span>
        </h5>
        <h5 className="movieInfo__info">
          Production:{" "}
          <span className="movieInfo__infoSpan">{movie.Production}</span>
        </h5>
        <h5 className="movieInfo__info">
          Genre: <span className="movieInfo__infoSpan">{movie.Genre}</span>
        </h5>
        <h5 className="movieInfo__info">
          Country: <span className="movieInfo__infoSpan">{movie.Country}</span>
        </h5>
        <h5 className="movieInfo__info">
          Premiered Date:{" "}
          <span className="movieInfo__infoSpan">{movie.Released}</span>
        </h5>
        <h5 className="movieInfo__info">
          Actors: <span className="movieInfo__infoSpan">{movie.Actors}</span>
        </h5>
        <h5 className="movieInfo__info">
          imdb Rating:{" "}
          <span className="movieInfo__infoSpan">{movie.imdbRating}</span>
        </h5>
        <p className="movieInfo__plot">{movie.Plot}</p>
      </div>
    </div>
  );
}

export default MovieInfo;
