import React from "react";
import { useHistory } from "react-router-dom";

import "../styles/MovieCard.scss";

function MovieCard(props) {
  let history = useHistory();
  const { title, imgSrc, year, id } = props;

  const handleBtnClick = () => {
    history.push("/movieInfoApp/movie/" + id);
  };
  return (
    <div className="movieCard">
      <img src={imgSrc} alt={title} className="movieCard__img" />
      <div className="movieCard__info">
        <h4 className="movieCard__title">
          {title.length > 15 ? title.slice(0, 15) + "..." : title}
        </h4>
        <h5 className="movieCard__year">{year}</h5>
        <button onClick={handleBtnClick} className="movieCard__btn">
          Show me more
        </button>
      </div>
    </div>
  );
}

export default MovieCard;
